<template>
    <v-card>
        <v-card-title class="pa-2 pl-4">{{ name }}</v-card-title>
        <div class="overflow-y-auto" style="max-height: 69vh">
            <v-card
                class="ma-3"
                max-width="400"
                v-for="card in cards"
                :key="card._id"
            >
                <v-img
                    class="white--text align-end"
                    height="200px"
                    src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
                    @click="viewCard(card._id, card.lane)"
                    style="cursor: pointer"
                >
                </v-img>
                <v-card-title>{{ card.title }}</v-card-title>
                <v-card-subtitle class="pt-1">
                    Due:
                    {{ utils.formatDate(card.dueDate) || "Not set!" }}
                </v-card-subtitle>
                <v-card-text class="text--primary">
                    <v-row class="px-3 py-2" justify="space-between">
                        <div>
                            <v-icon>mdi-account</v-icon>
                            {{ card.members || 8 }}
                        </div>
                        <div>
                            <v-icon>mdi-check</v-icon>
                            {{ card.completed }}/{{ card.tasks.length }}
                        </div>
                        <div>
                            <v-icon @click="deleteCard(card._id)"
                                >mdi-delete</v-icon
                            >
                        </div>
                        <v-progress-circular
                            :rotate="360"
                            :size="25"
                            :width="2"
                            :value="card.progress"
                            caption
                            :color="
                                card.progress <= 30
                                    ? 'red'
                                    : card.progress > 30 && card.progress <= 70
                                    ? 'yellow'
                                    : 'green'
                            "
                            ><div style="font-size: 0.7em">
                                {{ Math.ceil(card.progress) }}%
                            </div>
                        </v-progress-circular>
                    </v-row>
                </v-card-text>
            </v-card>
        </div>
        <v-card-actions v-if="addCard">
            <v-btn block dark @click="createCard">Create card</v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
import Vue from "vue";
import board from "@/services/board";
import config from "@/config";
import utils from "@/services/utils";
export default {
    name: "BoardColumn",
    props: {
        name: String,
        cards: Array,
        addCard: { type: Boolean, default: false },
    },
    data() {
        return {
            utils
        }
    },
    methods: {
        createCard() {
            this.$store.state.board.cardObject = {
                title: "New card",
                description: null,
                priority: null,
                dueDate: new Date(
                    Date.now() - new Date().getTimezoneOffset() * 60000
                )
                    .toISOString()
                    .substr(0, 10),
                tasks: [],
                image: null,
            };
            this.$store.state.board.showCardDialog = true;
        },
        viewCard(cardId, lane) {
            if (lane === "ToDo") {
                this.$store.state.board.cardObject =
                    this.$store.state.board.toDoArr.filter(
                        (card) => card._id === cardId
                    )[0];
            } else if (lane === "Doing") {
                this.$store.state.board.cardObject =
                    this.$store.state.board.doingArr.filter(
                        (card) => card._id === cardId
                    )[0];
            } else if (lane === "Done") {
                this.$store.state.board.cardObject =
                    this.$store.state.board.doneArr.filter(
                        (card) => card._id === cardId
                    )[0];
            }
            this.$store.state.board.showCardDialog = true;
        },
        async deleteCard(boardId) {
            this.$store.state.board.loading = true;
            try {
                await board.deleteCard(boardId);
                this.$emit("reload");
                Vue.$toast.open({
                            message: "Added to trash!",
                            duration: config.duration,
                            type: "error",
                        });
            } catch (error) {
                Vue.$toast.open({
                    message: error.response.data.message,
                    duration: config.duration,
                    type: "error",
                });
            }
        },
    },
};
</script>
