<template>
    <v-container class="pb-0">
        <ShowLoading></ShowLoading>
        <v-row>
            <v-col cols="7">
                <div class="text-h6" text>{{ boardData._doc.title }}</div>
            </v-col>
            <v-col align="end">
                <v-icon class="px-3" @click="showInfoFun">mdi-information</v-icon>
                <!-- <v-icon>mdi-account-multiple</v-icon>
                <v-icon>mdi-chevron-down</v-icon> -->
            </v-col>
        </v-row>
        <v-divider class="mt-2"></v-divider>
        <!-- board info -->
        <BoardInfo :boardData="boardData"></BoardInfo>
        <div
            style="background-color: none;"
            class="overflow-x-auto d-flex"
        >
            <div
                style="min-width: 375px; display: inline"
                class="pa-6"
                v-for="column in columns"
                :key="column"
            >
            <!-- board columns -->
                <BoardColumn
                    :name="column.name"
                    :cards="column.columnArray"
                    :addCard="column.addCard"
                    @reload="fetchCards"
                ></BoardColumn>
            </div>
        </div>
        <!-- card dialog -->
        <CardDialog @reload="fetchCards"></CardDialog>
    </v-container>
</template>

<script>
// import draggable from "vuedraggable";
import Vue from "vue";
import ShowLoading from "../ShowLoading.vue";
import BoardInfo from "./BoardInfo.vue";
import BoardColumn from "./BoardColumn.vue";
import CardDialog from "./CardDialog.vue";
import board from "@/services/board";
import config from "@/config";
export default {
    name: "CreateBoard",
    components: {
        ShowLoading,
        BoardInfo,
        BoardColumn,
        CardDialog,
    },
    data() {
        return {
            boardData: {},
            columns: [
                {
                    name: "ToDo",
                    columnArray: [],
                    addCard: true,
                },
                {
                    name: "Doing",
                    columnArray: [],
                },
                {
                    name: "Done",
                    columnArray: [],
                },
            ],
        };
    },

    methods: {
        showInfoFun() {
            this.$store.state.board.showBoardInfo = true;
        },
        async fetchCards() {
            try {
                const boardData = await board.fetchBoardById(
                    this.$route.params.id
                );
                this.boardData = boardData.data;
                const toDoArr = [];
                const doingArr = [];
                const doneArr = [];
                boardData.data._doc.cards.forEach((card) => {
                    if (card.lane === "ToDo") {
                        toDoArr.push(card);
                    } else if (card.lane === "Doing") {
                        doingArr.push(card);
                    } else if (card.lane === "Done") {
                        doneArr.push(card);
                    }
                    this.columns[0].columnArray = toDoArr;
                    this.columns[1].columnArray = doingArr;
                    this.columns[2].columnArray = doneArr;
                });
                this.$store.state.board.loading = false;
                this.$store.state.board.toDoArr = toDoArr;
                this.$store.state.board.doingArr = doingArr;
                this.$store.state.board.doneArr = doneArr;
            } catch (error) {
                Vue.$toast.open({
                            message: error.response.data.message,
                            duration: config.duration,
                            type: "error",
                        });
            }
        },
    },
    mounted() {
        this.$store.state.board.loading = true;
        this.fetchCards();
    },
};
</script>
