<template>
    <v-container>
        <ShowLoading></ShowLoading>
        <h1>Boards</h1>
        <v-divider class="my-3"></v-divider>
        <!-- <h3 class="my-3">Created by you</h3> -->
        <v-row>
            <v-col
                sm="6"
                md="4"
                lg="3"
                class="d-flex"
                v-for="board in ownBoards"
                :key="board.id"
            >
                <v-card class="px-auto d-flex flex-column">
                    <div
                        @click="openBoard(board._doc._id)"
                        style="cursor: pointer"
                    >
                        <v-img
                            class="white--text align-end"
                            height="200px"
                            src="https://cdn.vuetifyjs.com/images/cards/docks.jpg"
                        >
                        </v-img>
                        <v-card-title>{{ board._doc.title }}</v-card-title>

                        <v-card-subtitle class="py-1">
                            Due:
                            {{ utils.formatDate(board._doc.dueDate) ||
                                "Not set!"
                            }}
                        </v-card-subtitle>
                    </div>
                    <v-card-text class="text--primary">
                        {{ utils.sliceStr(board._doc.description) || "No description :("}}
                        <div v-if="board._doc.description && (board._doc.description).length>100"
                            class="read-more"
                            @click="openBoard(board._doc._id)"
                        >
                            read more
                        </div>
                    </v-card-text>
                    <v-spacer></v-spacer>
                    <v-card-text>
                        <v-row class="px-3 pb-2" justify="space-between">
                            <div>
                                <v-icon>mdi-account</v-icon>
                                {{ board.members || 5 }}
                            </div>
                            <div>
                                <v-icon
                                    :color="
                                        board._doc.liked ? 'red' : 'default'
                                    "
                                    @click="likeBoard(board._doc._id)"
                                    >mdi-heart</v-icon
                                >
                            </div>
                            <div>
                                <v-icon @click="deleteBoard(board._doc._id)"
                                    >mdi-delete</v-icon
                                >
                            </div>
                            <div>
                                <v-icon>mdi-check</v-icon>
                                {{ board.completed }}/{{ board.total }}
                            </div>
                        </v-row>
                        <v-progress-linear
                            class="my-4"
                            v-model="board.progress"
                            :color="
                                board.progress <= 30
                                    ? 'red'
                                    : board.progress > 30 &&
                                      board.progress <= 70
                                    ? 'yellow'
                                    : 'green'
                            "
                            height="15"
                            rounded
                        >
                            <template v-slot:default="{ value }">
                                <strong>{{ Math.ceil(value) }}%</strong>
                            </template>
                        </v-progress-linear>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
        <!-- <v-divider class="mt-3"></v-divider>
        <h3 class="my-3">Boards you are part of</h3> -->

        <!-- create board dialog -->
        <div class="text-center">
            <v-dialog v-model="dialog" width="800">
                <v-card>
                    <v-form ref="newBoardData">
                        <v-card-title class="text-h5 grey lighten-2"
                            >Create board</v-card-title
                        >
                        <v-card-text class="pt-6">
                            <v-text-field
                                v-model="newBoardData.title"
                                label="Title"
                                solo
                                prepend-icon="mdi-file-chart-check-outline"
                                :rules="[rules.required]"
                            ></v-text-field>
                            <v-textarea
                                v-model="newBoardData.description"
                                solo
                                auto-grow
                                rows="1"
                                counter
                                clearable
                                clear-icon="mdi-close-circle"
                                label="Description"
                                prepend-icon="mdi-text"
                            ></v-textarea>
                            <v-menu
                                ref="showDatePicker"
                                v-model="showDatePicker"
                                :close-on-content-click="false"
                                transition="scale-transition"
                                offset-y
                                max-width="290px"
                                min-width="auto"
                            >
                                <template v-slot:activator="{ on, attrs }">
                                    <v-text-field
                                        v-model="formatDate"
                                        solo
                                        label="Due date"
                                        prepend-icon="mdi-calendar"
                                        readonly
                                        v-bind="attrs"
                                        v-on="on"
                                    ></v-text-field>
                                </template>
                                <v-date-picker
                                    v-model="newBoardData.dueDate"
                                    no-title
                                    @input="showDatePicker = false"
                                ></v-date-picker>
                            </v-menu>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn text @click="dialog = false">Close</v-btn>
                            <v-btn text @click="createBoard">Create</v-btn>
                        </v-card-actions>
                    </v-form>
                </v-card>
            </v-dialog>
        </div>
        <v-btn
            fab
            bottom
            right
            fixed
            large
            dark
            class="ma-16"
            @click="dialog = !dialog"
        >
            <v-icon>mdi-plus</v-icon>
        </v-btn>
    </v-container>
</template>
<script>
import Vue from "vue";
import ShowLoading from "@/components/ShowLoading.vue";
import config from "@/config";
import board from "@/services/board";
import utils from "@/services/utils";
export default {
    name: "UserBoards",
    components: {
        ShowLoading,
    },
    data() {
        return {
            error: null,
            rules: {
                required: (value) => !!value || "Required.",
            },
            showDatePicker: false,
            dialog: false,
            dateMenu: false,
            newBoardData: this.resetBoardData(),
            ownBoards: [],
            utils,
            teamBoards: [],
        };
    },
    computed: {
        loading() {
            return this.$store.state.board.loading;
        },
        formatDate() {
            return utils.formatDate(this.newBoardData.dueDate);
        },
    },
    methods: {
        resetBoardData() {
            return {
                title: null,
                description: null,
                dueDate: new Date(
                    Date.now() - new Date().getTimezoneOffset() * 60000
                )
                    .toISOString()
                    .substr(0, 10),
                image: null,
            };
        },
        async fetchBoards() {
            try {
                const response = await board.fetchBoards();
                this.ownBoards = response.data;
                this.$store.state.board.loading = false;
            } catch (error) {
                this.error = error;
            }
        },
        async createBoard() {
            if (this.$refs.newBoardData.validate()) {
                this.dialog = false;
                this.$store.state.board.loading = true;
                try {
                    await board.createBoard(this.newBoardData);
                    this.fetchBoards();
                    this.newBoardData = this.resetBoardData();
                    Vue.$toast.open({
                        message: "Woahh!!, a board was created",
                        duration: config.duration,
                        type: "success",
                    });
                } catch (error) {
                    Vue.$toast.open({
                        message: error.response.data.message,
                        duration: config.duration,
                        type: "error",
                    });
                }
            }
        },
        async likeBoard(boardId) {
            // this.$store.state.board.loading = true;
            try {
                await board.likeBoard(boardId);
                this.fetchBoards();
            } catch (error) {
                Vue.$toast.open({
                    message: error.response.data.message,
                    duration: config.duration,
                    type: "error",
                });
            }
        },
        openBoard(boardId) {
            this.$store.state.board.loading = true;
            this.$router.push({
                name: "board-details",
                params: { id: boardId },
            });
        },
        async deleteBoard(boardId) {
            this.$store.state.board.loading = true;
            try {
                await board.deleteBoard(boardId);
                this.fetchBoards();
                Vue.$toast.open({
                    message: "Filling trash ;(",
                    duration: config.duration,
                    type: "error",
                });
            } catch (error) {
                Vue.$toast.open({
                    message: error.response.data.message,
                    duration: config.duration,
                    type: "error",
                });
            }
        },
    },
    mounted() {
        this.$store.state.board.loading = true;
        this.fetchBoards();
    },
};
</script>
<style>
.read-more {
    display: inline;
    color: rgb(0, 0, 0, 0.5);
    cursor: pointer;
}
</style>
